@use '../util' as *;
/// second section
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: rem(23);

  .card__subtitle {
    font-size: rem(14);
    font-weight: 700;
  }
  .card__count {
    margin-bottom: 0;
  }
  .card__count,
  .card__change {
    align-self: end;
  }
  img,
  .card__change {
    justify-self: end;
  }
}
